html {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App-header {
  width: 100%;
  height: 20px;
  padding: 0;
  margin: 0;
  background-color: gray;
}

.navbar {
  width: 100%;
  height: 20px;

  background-color: #59ac7a;
}

.data-creation-form {
  width: 100%;
  margin: auto;
  padding: 20px;
}

.data-viewer {
  border-top: 1px solid black;
  display: flex;
  flex: justify-content;
  width: 100%;
  margin: auto;
  padding: 20px;
}

.data-viewer div {
  width: 100%;
  margin: auto
}

.column{
  width: 20%
}

label,
input {
  display: block;
}

.body-wrapper {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  vertical-align: top;
}

.data-lists {
  display: flex;
  justify-content: flex-start;
  width: 40%;
  overflow: auto;
}
.data-lists div {
  margin-right: 10px;
}

.creation-view {
  width: 60%;
}
.creation-view div {
  width: 100%%;
}

.data-view div {
  width: 100%;
}
.provisional{
  color: green;
  padding-left: 10px;
  font-size: 12px;
}

.imageWrapper{
  width: 100%;

}
.imageWrapper img{
  width: 100%
}
